export default {

	data() {
		return {
			phone: '',
			code: '',
			opcity: 0.5,
			nosendCode: false,
			sendbtn: {
				text: '获取验证码',
				codeTime: 60
			},
		}
	},
	onLoad() {

	},
	onReady() {

	},
	methods: {
		sendCode() {
			//请求短信验证码
			uni.request({
				url: this.baseUrl + "/sms-bindphone-code",
				data: {
					Phone: this.phone,
				},
				method: 'POST',
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						this.sendMsgCodeTimer()
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		comfirme() {
			//绑定手机号
			uni.request({
				url: this.baseUrl + "/bind-phone",
				data: {
					Phone: this.phone,
					AuthCode: this.code,
				},
				method: 'POST',
				header: {
					//除注册登录外其他的请求都携带用户token和秘钥
					Authorization: uni.getStorageSync('token')
				},
				success: (res) => {
					console.log(res.data)
					if (this.$base1._indexOf(res.data.status)) {
						this.$base1._isLogin()
					} else if (res.data.status == 1) {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
						uni.navigateTo({
							url: "./personal"
						})
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				}
			})
		},
		change(e) {
			if (e.detail.value.length >= 10) {
				this.opcity = 1
			} else {
				this.opcity = 0.5
			}
		},
		
		// 发送短信验证码计时器
		sendMsgCodeTimer() {
			this.timerId = setInterval(() => {
				let codeTime = this.sendbtn.codeTime;
				codeTime--;
				this.sendbtn.codeTime = codeTime;
				this.sendbtn.text = codeTime + "S";
				if (codeTime < 1) {
					clearInterval(this.timerId);
					this.sendbtn.text = "重新获取";
					this.nosendCode = false
					this.sendbtn.codeTime = 60;
					this.$refs.getCode1.$el.style.backgroundColor = "rgba(127, 204, 255, 1)"
				}
			}, 1000);
		},
	}
}
